<template>
  <div id="settle-report">
    <div class="left">
      <el-menu
        default-active="1-1"
        class="el-menu-vertical-demo"
        @click.native="menuClick"
      >
        <el-submenu index="1">
          <template slot="title">
            <span>订单</span>
          </template>
          <el-menu-item index="1-1">
            <span slot="title">所有订单报告</span>
          </el-menu-item>
          <el-menu-item index="1-2">
            <span slot="title">已完成订单报告</span>
          </el-menu-item>
        </el-submenu>
        <el-menu-item index="2">
          <span slot="title">交易报告</span>
        </el-menu-item>
        <el-menu-item index="3">
          <span slot="title">汇总报告</span>
        </el-menu-item>
        <el-menu-item index="4">
          <span slot="title">结算周期报告</span>
        </el-menu-item>
        <el-menu-item index="5">
          <span slot="title">库存报告</span>
        </el-menu-item>
        <el-menu-item index="6">
          <span slot="title">VAT报告</span>
        </el-menu-item>
        <el-menu-item index="7">
          <span slot="title">退货报告</span>
        </el-menu-item>
        <el-menu-item index="8">
          <span slot="title">退款报告</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="right">
      <!-- 选择下拉框相关 -->
      <sort-selection
        :title="title"
        :type="type"
        ref="selection"
        @createReport="createReport"
        @getVatReport="getVatReport"
        @queryVatReport="queryVatReport"
        @getStockReport="getStockReport"
        @getDealGatherSettleList="getDealGatherSettleList"
        @refresh="refresh"
        @getReturnData="dispatchReturn"
      ></sort-selection>
      <!-- 生成的订单列表 -->
      <order-list
        :selectOpt="selectOpt"
        :type="type"
        :orderList="orderList"
        v-if="orderList.length > 0"
      ></order-list>
      <!-- VAT报告列表 -->
      <vat-list v-if="isVatReport" ref="vat" :vatList="vatList"></vat-list>
      <!-- 库存报告列表 -->
      <stock-list
        v-if="isStockReport && stockList.length > 0"
        ref="stock"
        :stockList="stockList"
      ></stock-list>
      <!-- 生成的汇总列表和交易列表和周期结算列表 -->
      <order-list-two
        v-if="isDealAndGatherReport && dgsList.length > 0"
        :orderList="dgsList"
      ></order-list-two>
      <!-- 退货报告Return report 和退款报告 Refund report -->
      <return-list
        v-if="isReturn && return_list.length > 0"
        :return_list="return_list"
      ></return-list>
    </div>
  </div>
</template>
<script>
import SortSelection from "components/dataBase/settlementchild/SortSelection";
import OrderList from "components/dataBase/settlementchild/OrderList";
import VatList from "components/dataBase/settlementchild/VatList";
import StockList from "components/dataBase/settlementchild/StockList";
import OrderListTwo from "components/dataBase/settlementchild/OrderListTwo";
// 退货报告退款报告
import ReturnList from "components/dataBase/settlementchild/ReturnList";
export default {
  name: "",
  props: {},
  components: {
    SortSelection,
    OrderList,
    VatList,
    StockList,
    OrderListTwo,
    ReturnList
  },
  data() {
    return {
      selectOpt: {},
      title: "所有订单报告",
      // 接口需传一个type
      // type: "report",
      type: "report",
      // 订单列表
      orderList: [],
      // VAT报告渲染列表所需数据
      fileName: [],
      monthList: [],
      downloadPath: [],
      vatList: [],
      // 查询库存url
      stockPath: "",
      stockList: [],
      // 交易报告和汇总报告和周期结算的数据列表
      dgsList: [],
      return_list: [] //退货报告，退款报告数据列表
    };
  },
  computed: {
    isVatReport() {
      return this.title === "VAT报告";
    },
    isStockReport() {
      return this.title === "库存报告";
    },
    // 判断是不是交易报告和汇总报告和周期结算报告
    isDealAndGatherReport() {
      return (
        this.title === "交易报告" ||
        this.title === "汇总报告" ||
        this.title === "结算周期报告"
      );
    },
    // 判断是不是退货报告或者退款报告
    isReturn() {
      return this.title === "退货报告" || this.title === "退款报告";
    }
  },
  watch: {},
  methods: {
    dispatchReturn(data) {
      this.return_list = data || [];
    },
    // 菜单点击事件
    menuClick(ev) {
      console.log(ev.target.innerText);

      switch (ev.target.innerText) {
        case "所有订单报告":
          this.title = "所有订单报告";
          this.type = "report";
          break;
        case "已完成订单报告":
          this.title = "已完成订单报告";
          this.type = "completed_report";
          break;
        case "交易报告":
          this.title = "交易报告";
          break;
        case "汇总报告":
          this.title = "汇总报告";
          break;
        case "结算周期报告":
          this.title = "结算周期报告";
          break;
        case "库存报告":
          this.title = "库存报告";
          this.stockList = [];
          break;
        case "VAT报告":
          this.vatList = [];
          this.title = "VAT报告";
          this.$api.getVatList().then(res => {
            console.log(res);
            this.fileName = res.data.ret_files_name_list;
            this.monthList = res.data.month_list;
            // this.downloadPath = res.data.download_path;
            // 利用上面三个来获取vatList
            for (let i in this.monthList) {
              let fileName = this.fileName[i];
              // let downloadPath = this.downloadPath[i];
              // filename和downloadpath同级，遍历谁都行
              for (let key in fileName) {
                let obj = {};
                obj["fileName"] = fileName[key];
                // obj["downloadPath"] = downloadPath[key];
                obj["month"] = this.monthList[i];
                this.vatList.push(obj);
              }
            }
            // 强制刷新
            this.$forceUpdate();
            console.log(this.vatList);
          });
          break;
        // 新增
        case "退货报告":
          this.title = "退货报告";
          this.return_list = [];
          break;
        case "退款报告":
          this.title = "退款报告";
          this.return_list = [];
          break;
      }
      // 只要点击菜单栏，就应该另下拉框的内容置空，同时要清空orderList
      if (ev.target.innerText !== "订单") {
        let sel = this.$refs.selection.selection;
        for (let i in sel) {
          sel[i] = "";
        }
        for (let i in this.$refs.selection.stockSelection) {
          this.$refs.selection.stockSelection[i] = "";
        }
        this.$refs.selection.stockSelection.type = "库存报告";
        this.$refs.selection.isDateDisabled = false;
        this.$refs.selection.isMonthDisabled = false;
        this.$refs.selection.isStockDateDisabled = false;
        this.$refs.selection.isStockMonthDisabled = false;
        this.$refs.selection.siteList = [];
        this.$refs.selection.stockSiteList = [];
        this.orderList = [];
        this.dgsList = [];
        this.stockList = [];
      }
    },
    // 子组件SortSelection传过来的事件，获取到生成的订单列表
    createReport(data, selection) {
      console.log(data);
      this.orderList = data;
      this.selectOpt = selection;
    },
    getVatReport(path, fileName) {
      console.log(path);
      console.log(fileName);
    },
    queryVatReport(month) {
      console.log("asdsasdsa");
      this.vatList = [];
      this.$api.getVatList(month).then(res => {
        console.log(res);
        if (!res) return;
        this.fileName = res.data.ret_files_name_list;
        this.monthList = res.data.month_list;
        // this.downloadPath = res.data.download_path;
        // 利用上面三个来获取vatList
        for (let i in this.monthList) {
          let fileName = this.fileName[i];
          // let downloadPath = this.downloadPath[i];
          // filename和downloadpath同级，遍历谁都行
          for (let key in fileName) {
            let obj = {};
            obj["fileName"] = fileName[key];
            // obj["downloadPath"] = downloadPath[key];
            obj["month"] = this.monthList[i];
            this.vatList.push(obj);
          }
        }
        // 强制刷新
        this.$forceUpdate();
        console.log(this.vatList);
      });
    },
    refresh() {
      this.$api.getVatList().then(res => {
        this.vatList = [];
        console.log(res);
        if (!res) return;
        this.fileName = res.data.ret_files_name_list;
        this.monthList = res.data.month_list;
        // this.downloadPath = res.data.download_path;
        // 利用上面三个来获取vatList
        for (let i in this.monthList) {
          let fileName = this.fileName[i];
          // filename和downloadpath同级，遍历谁都行
          for (let key in fileName) {
            let obj = {};
            obj["fileName"] = fileName[key];
            // obj["downloadPath"] = downloadPath[key];
            obj["month"] = this.monthList[i];
            this.vatList.push(obj);
          }
        }
        // 强制刷新
        this.$forceUpdate();
        console.log(this.vatList);
      });
    },
    // 获取库存报告
    getStockReport(path, sel) {
      // 将库存报告列表置空
      this.stockList = [];
      this.stockPath = path;
      // 只有一行数据，但是也要让table渲染的数组中要套一个数组，这样才符合el-table格式
      let arr = [];
      if (sel.date !== "")
        arr.push(sel.platform, sel.site, sel.country, sel.date, path);
      else arr.push(sel.platform, sel.site, sel.country, sel.month, path);
      this.stockList.push(arr);
    },
    getDealGatherSettleList(data) {
      this.dgsList = data;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
#settle-report {
  width: 100vw;
  height: 92vh;
  position: relative;
  /* margin-top: 2vh; */
}
#settle-report >>> input[type="file"] {
  display: none;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
}
.right {
  position: absolute;
  top: 0;
  left: 18vw;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 24px;
}
</style>



