<template>
  <div>
    <el-table :data="vatList" border style="width: 46%" stripe>
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="month" label="月份"></el-table-column>
      <el-table-column prop="fileName" label="报告">
        <template slot-scope="scope">
          <a
            :href="'https://www.beyoung.group/file_download/?path=operation/VAT_report/'+scope.row.month.replace(/-/,'_')+'/&filename='+scope.row.fileName"
            :download="scope.row.fileName"
          >{{scope.row.fileName}}</a>
          <!-- <el-link
            type="primary"
            @click="downloadUrlFile(_url2+scope.row.downloadPath)"
          >{{scope.row.fileName}}</el-link>-->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    vatList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  components: {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  methods: {
    // 获取页面文件名
    downloadUrlFile(url) {
      url = url.replace(/\\/g, "/");
      const xhr = new XMLHttpRequest();
      xhr.open("GET", url, true);
      xhr.responseType = "blob";
      //xhr.setRequestHeader('Authorization', 'Basic a2VybWl0Omtlcm1pdA==');
      xhr.onload = () => {
        if (xhr.status === 200) {
          // 获取文件blob数据并保存
          var fileName = this.getFileName(url);
          this.saveAs(xhr.response, fileName);
        }
      };
      xhr.send();
    },
    saveAs(data, name) {
      var urlObject = window.URL || window.webkitURL || window;
      var export_blob = new Blob([data]);
      var save_link = document.createElementNS(
        "http://www.w3.org/1999/xhtml",
        "a"
      );
      save_link.href = urlObject.createObjectURL(export_blob);
      save_link.download = name;
      save_link.click();
    },
    getFileName(url) {
      var num = url.lastIndexOf("/") + 1;
      var fileName = url.substring(num);
      //把参数和文件名分割开
      fileName = decodeURI(fileName.split("?")[0]);
      return fileName;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
</style>


