<template>
  <div class="sort-selection">
    <div>
      <span class="titleSpan">{{ title }}</span>
      <el-button
        class="upload_btn"
        @click="showDealAndGather"
        type="primary"
        size="mini"
        v-if="isDealAndGatherReport"
