    <!-- 订单 -->
    <div class="all-select" v-if="isOrderReport">
      <div class="select">
        <span>渠道:</span>
        <el-select
          v-model="selection.platform"
          placeholder="请选择"
          size="small"
        >
          <el-option
            v-for="(item, index) in platformList"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </div>
      <div class="select">
        <span>国家:</span>
        <el-select
          v-model="selection.country"
          placeholder="请选择"
          size="small"
          @change="countrySelectChange"
        >
          <el-option
            v-for="(item, index) in countryList_"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </div>
      <div class="select">
        <span>站点:</span>
        <el-select v-model="selection.site" placeholder="请选择" size="small">
          <el-option
            v-for="(item, index) in siteList"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </div>

      <div class="select">
        <span>时间：</span>
        <el-date-picker
          v-model="selection.date"
          align="right"
          type="date"
          placeholder="选择日期"
          :picker-options="pickerOptions"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          @change="pickerChange('日期')"
          :disabled="isDateDisabled"
          size="small"
        ></el-date-picker>
      </div>
      <div class="select month">
        <span>或月份：</span>
        <el-date-picker
          v-model="selection.month"
          type="month"
          placeholder="选择日期"
          format="yyyy-MM"
          value-format="yyyy-MM"
          @change="pickerChange('月份')"
          :disabled="isMonthDisabled"
          size="small"
          :picker-options="monthPickerOptions1"
        ></el-date-picker>
        <el-button
          type="primary"
          size="mini"
          @click="creatReport"
          style="margin-left:6px"
          >生成</el-button
        >
      </div>
    </div>
    <!-- VAT报告展示部分 -->
    <div class="all-select" v-if="isVatReport">
      <div class="select month" style="display:flex;">
        <span>月份：</span>
