<script>
export default {
  name: "",
  props: {
    title: {
      type: String,
      default() {
        return "";
      }
    },
    type: {
      type: String,
      default() {
        return "";
      }
    }
  },
  components: {},
  data() {
    return {
      returnFile: [],
      selection: {
        platform: "",
        site: "",
        country: "",
        date: "",
        month: "",
        type: "",
        page_type: "",
        select_month: "",
        daterange: []
      },
      // 渠道列表
      platformList: ["Amazon"],
      // 站点列表
      siteList: [],
      // 国家列表
      countryList: [],
      // 时间快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      monthPickerOptions1: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      monthPickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      // 日期，月份是否禁用
      isDateDisabled: false,
      isMonthDisabled: false,
      isStockDateDisabled: false,
      isStockMonthDisabled: false,
      // VAT上传文件
      file: "",
      // stockPlatFormList: [],
      stockCountryList: [],
      stockSiteList: [],

      stockSelection: {
        platform: "Amazon",
        site: "",
        country: "",
        date: "",
        month: "",
        type: "库存报告"
      },
      formRules: {
        platform: [
          { required: true, message: "请选择渠道", trigger: "change" }
        ],
        site: [{ required: true, message: "请选择站点", trigger: "change" }],
        country: [{ required: true, message: "请选择国家", trigger: "change" }],
        select_month: [
          {
            type: "string",
            required: true,
            message: "请选择月份",
            trigger: "change"
          }
        ],
        daterange: [
          {
            type: "array",
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ]
      },
      // 汇总报告和交易报告对话框
      dealAndGatherVisible: false,
      dgForm: {
        platform: "",
        site: "",
        country: "",
        date: "",
        month: "",
        type: "",
        page_type: "",
        select_month: "",
        daterange: []
      },
      settleVisible: false,
      settleForm: {
        platform: "",
        site: "",
        country: "",
        date: "",
        month: "",
        type: "",
        page_type: "",
        select_month: "",
        daterange: []
      },
      // 退款报告，退货报告
      returnReportVisible: false,
      rpForm: {
        platform: "",
        country: "",
        site: "",
        select_month: ""
      },
      returnType: ""
    };
  },
  computed: {
    // 将拿到的国家中法国，德国，意大利，西班牙改成欧洲：
    countryList_() {
      let flag = false;
      let list = [];
      for (let key in this.countryList) {
        let i = this.countryList[key];
        if (
          i.includes("法国") ||
          i.includes("德国") ||
          i.includes("意大利") ||
          i.includes("西班牙")
        ) {
          flag = true;
          continue;
        }
        list.push(i);
      }
      if (flag === true) {
        list.push("欧洲");
      }
      console.log(this.countryList, list);
      return list;
    },
    handleExceed() {
      this.$message.error("只允许上传一个文件");
    },
    // 先用计算属性拿到日期
    seleteDate() {
      return this.selection.date;
    },
    seleteMonth() {
      return this.selection.month;
    },
    stockSelectionDate() {
      return this.stockSelection.date;
    },
    stockSelectionMonth() {
      return this.stockSelection.month;
    },
    // 判断是否是vat报告
    isVatReport() {
      return this.title === "VAT报告";
    },
    // 判断是否是库存报告
    isStockReport() {
      return this.title === "库存报告";
    },
    // 判断是不是订单（所有订单报告和已完成订单报告）
    isOrderReport() {
      return this.title === "所有订单报告" || this.title === "已完成订单报告";
    },
    // 判断是不是交易报告和汇总报告
    isDealAndGatherReport() {
      return this.title === "汇总报告" || this.title === "交易报告";
    },
    // 判断是不是结算周期报告
    isSettleReport() {
      return this.title === "结算周期报告";
    },
    // 判断是否退货退款报告
    isReturnReport() {
      if (this.title === "退货报告") {
        this.returnType = "退货报告";
      }
      if (this.title === "退款报告") {
        this.returnType = "退款报告";
      }
      return this.title === "退货报告" || this.title === "退款报告";
    }
  },
  watch: {
    // 监听日期变化 如果日期被清除，应该要让月份可以选
    seleteDate(date) {
      if (date === null) {
        this.isMonthDisabled = false;
      }
    },
    seleteMonth(month) {
      if (month === null) {
        this.isDateDisabled = false;
      }
    },
    stockSelectionDate(date) {
      if (date === null) {
        this.isStockMonthDisabled = false;
      }
    },
    stockSelectionMonth(month) {
      if (month === null) {
        this.isStockDateDisabled = false;
      }
    }
  },
  methods: {
    returnReport() {
      this.returnReportVisible = true;
    },
    rpDialogClose() {
      console.log("关闭");
      this.$refs.rpForm.resetFields();
      this.$refs.uploadRp.clearFiles();
      this.file = "";
      this.returnFile = [];
      this.returnReportVisible = false;
    }, // 关闭退货退款报告
    beforeUploadRp(file) {
      this.file = "";
      this.returnFile = [];
      console.log("上传之前", file);
      if (!/\.txt$/g.test(file.name)) {
        alert("请上传txt文件!");
        // this.file = "";
        // this.returnFile = [];
        return;
      }
      this.file = file;
      this.returnFile.push(file);
    }, // 上传前

    uploadFileRp(file) {
      // 选择文件后触发
      console.log("文件上传", file);
    }, //自定上传方法
    uploadSuccessRp() {
      console.log("上传成功");
    }, //上传成功
    handleRemoveRp() {
      this.file = "";
      this.returnFile = [];
    }, // 移除列表
    uploadReturnRp(refName) {
      // 点击确定时触发
      console.log(this.file);
      console.log("确认上传？？？");
      // 如果退货报告和退款报告相同
      this.$refs[refName].validate(valid => {
        if (!valid) return;
        if (this.file === "") {
          this.$message.closeAll();
          return this.$message.error("请选择一个文件");
        }
        console.log(this.file);
        let formData = new FormData();
        // formData.append(xx,xx); // 遍历form表单参数
        // 请求方法
        formData.append("platform", this.rpForm.platform);
        formData.append("country", this.rpForm.country);
        formData.append("site", this.rpForm.site);
        formData.append("month", this.rpForm.select_month);
        formData.append("file_path", this.file);
        formData.append("type", this.returnType);
        // 清空文件列表
        this.$refs["uploadRp"].clearFiles();
        // 发起请求
        let url = this._url2 + "server/databases/return_report/";
        this.$axios.post(url, formData).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.$message.success("请求成功");
            this.returnReportVisible = false;
            for (let key in this.rpForm) {
              this.rpForm[key] = "";
            }
          }
        });
      });
    }, // 确认上传
    // 页面刷新获取下拉框数据
    getSelectList() {
      this.$api.getSelectList().then(res => {
        console.log(res);
        this.siteList = res.data.data;
      });
    },
    // 站点改变，渲染国家列表
    siteChange(site) {
      for (let i in this.siteList) {
        const item = this.siteList[i];
        if (item.store == site) {
          this.countryList = item.countries;
        }
      }
    },
    getStockSelectList() {
      this.$api.getPageSelect("库存报告").then(res => {
        console.log(res);
        this.stockCountryList = res.data.country_list;
        this.stockSiteList = res.data.site_list;
      });
    },
    /**
     * @description: 获取国家列表
     * @param {*}
     * @return {*}
     */
    getCountryList() {
      this.$api.getCountryList().then(res => {
        this.countryList = res.data.data;
        this.stockCountryList = res.data.data;
      });
    },
    /**
     * @description: 国家选择更新站点列表
     * @param {*} val
     * @return {*}
     */
    countrySelectChange(val) {
      this.selection.site = "";
      this.siteList = [];
      this.$api.getSiteList(val).then(res => {
        console.log(res);
        this.siteList = res.data.data;
        this.stockSiteList = res.data.data;
      });
    },
    // 日期月份二选一
    pickerChange(val) {
      if (this.title == "库存报告") {
        console.log("aaa");

        if (val == "日期") {
          this.isStockMonthDisabled = true;
        } else {
          this.isStockDateDisabled = true;
        }
      } else {
        if (val == "日期") {
          this.isMonthDisabled = true;
        } else {
          this.isDateDisabled = true;
        }
      }
    },

    // 点击生成
    creatReport() {
      this.selection.type = this.type;
      // console.log(this.selection);

      const sel = this.selection;
      if (!sel.platform || !sel.site || !sel.country) {
        this.$message.closeAll();
        this.$message.error("请选择选项");
        return;
      }
      if (!sel.date && !sel.month) {
        this.$message.closeAll();
        this.$message.error("请选择日期或月份");
        return;
      }
      this.$api.getAllOrder(this.selection).then(res => {
        console.log(res);
        this.$emit("createReport", res.data.data, this.selection);
      });
    },

    // 针对VAT报告的查询
    queryVatReport() {
      console.log(this.selection.month);
      this.$emit("queryVatReport", this.selection.month);
    },
    // 针对VAT报告的上传文件
    beforeUpload(file) {
      console.log(file);
      // this.fileData.push(file);
      this.file = file;
      let formData = new FormData();
      formData.append("file", this.file);
      this.$api.uploadVatReport(formData).then(res => {
        console.log(res);
        this.file = "";
        this.$message.success("上传成功");
        this.$emit("refresh");
      });
    },
    uploadFile(file) {},
    // 文件移除时触发
    handleRemove(file) {
      this.file = "";
    },
    // 针对库存报告
    queryStockReport() {
      const sel = this.stockSelection;

      if (!sel.platform || !sel.site || !sel.country) {
        this.$message.closeAll();
        this.$message.error("请选择选项");
        return;
      }
      if (!sel.date && !sel.month) {
        this.$message.closeAll();
        this.$message.error("请选择日期或月份");
        return;
      }
      this.$api.getStockList(this.stockSelection).then(res => {
        console.log(res);
        if (res.data.code != 200) {
          return this.$message.error(res.data.msg);
        }
        this.$emit(
          "getStockReport",
          res.data.download_path,
          this.stockSelection
        );
      });
    },
    getReturnReport() {
      if (
        this.selection["platform"] === "" ||
        this.selection["country"] === "" ||
        this.selection["site"] === ""
      ) {
        this.$message.warning("请选择除了国家外的选项");
        return;
      }
      let url = this._url2 + "server/databases/return_report/";
      let queryParams = {};
      queryParams["platform"] = this.selection.platform;
      queryParams["country"] = this.selection.country;
      queryParams["site"] = this.selection.site;
      queryParams["month"] = this.selection.select_month;
      queryParams["type"] = this.returnType;
      this.$axios
        .get(url, {
          params: queryParams
        })
        .then(res => {
          console.log("查询数据", res);
          if (res.data.re_data.length === 0) {
            this.$message.warning("查询不到数据!");
          } else {
            this.$emit("getReturnData", res.data.re_data);
          }
        });
    },
    // 针对交易报告和汇总报告和结算周期报告
    queryDealOrGatherSettleReport(title) {
      console.log(title);
      const sel = this.selection;
      if (!sel.platform || !sel.site || !sel.country) {
        this.$message.closeAll();
        this.$message.error("请选择选项，时间可选");
        return;
      }
      this.selection.page_type = this.title;
      this.$api.getDealGatherSettleList(this.selection).then(res => {
        console.log(res);
        if (res.data.code != 200) {
          this.$message.closeAll();
          return this.$message.error("没有符合条件的数据");
        }
        this.$emit("getDealGatherSettleList", res.data.data);
      });
    },
    uploadDealGatherSettle(file) {
      this.file = file;
      this.selection.page_type = this.title;
      this.dgForm.page_type = this.title;
      this.settleForm.page_type = this.title;
      // let formData = new FormData();
      // formData.append("platform", this.selection.platform);
      // formData.append("country", this.selection.country);
      // formData.append("site", this.selection.site);
      // formData.append("file", file);
      // formData.append("page_type", this.selection.page_type);
      // if (this.title == "结算周期报告") {
      //   console.log("是结算报告");
      //   console.log(this.selection.daterange[0]);
      //   console.log(this.selection.daterange[1]);

      //   formData.append("start_date", this.selection.daterange[0]);
      //   formData.append("end_date", this.selection.daterange[1]);
      // }
      // this.$api.uploadDealGatherSettle(formData).then(res => {
      //   console.log(res);
      //   this.$api.getDealGatherSettleList(this.selection).then(res => {
      //     console.log(res);
      //     this.$emit("getDealGatherSettleList", res.data.data);
      //   });
      // });
    },
    uploadSuccess(file) {
      // console.log('clear');
      // this.$refs.upload.clearFiles();
    },
    // 展示汇总报告和交易报告上传对话框
    showDealAndGather() {
      this.stockSiteList = [];
      this.dealAndGatherVisible = true;
    },
    // 文件上传
    uploadDealAndGather() {
      if (this.title !== "结算周期报告") {
        this.$refs.dealgatherRef.validate(valid => {
          if (!valid) return;
          if (this.file == "") {
            this.$message.closeAll();
            return this.$message.error("请选择一个文件");
          }
          let formData = new FormData();
          formData.append("platform", this.dgForm.platform);
          formData.append("country", this.dgForm.country);
          formData.append("site", this.dgForm.site);
          formData.append("file", this.file);
          formData.append("page_type", this.dgForm.page_type);
          formData.append("target_month", this.dgForm.select_month);
          if (this.title == "结算周期报告") {
            console.log("是结算报告");
            console.log(this.dgForm.daterange[0]);
            console.log(this.dgForm.daterange[1]);

            formData.append("start_date", this.dgForm.daterange[0]);
            formData.append("end_date", this.dgForm.daterange[1]);
          }
          this.$api.uploadDealGatherSettle(formData).then(res => {
            console.log(res);
            this.dealAndGatherVisible = false;
            this.$message.success("上传成功");
            this.$api.getDealGatherSettleList(this.dgForm).then(res => {
              console.log(res);
              this.$emit("getDealGatherSettleList", res.data.data);
            });
          });
          this.$refs.upload.clearFiles();
          this.file = "";
          this.stockSiteList = [];
        });
      } else {
        this.$refs.settleRef.validate(valid => {
          if (!valid) return;
          if (this.file == "") {
            this.$message.closeAll();
            return this.$message.error("请选择一个文件");
          }
          let formData = new FormData();
          formData.append("platform", this.settleForm.platform);
          formData.append("country", this.settleForm.country);
          formData.append("site", this.settleForm.site);
          formData.append("file", this.file);
          formData.append("page_type", this.settleForm.page_type);
          if (this.title == "结算周期报告") {
            console.log("是结算报告");
            console.log(this.settleForm.daterange[0]);
            console.log(this.settleForm.daterange[1]);

            formData.append("start_date", this.settleForm.daterange[0]);
            formData.append("end_date", this.settleForm.daterange[1]);
          }
          this.$api.uploadDealGatherSettle(formData).then(res => {
            console.log(res);
            this.settleVisible = false;
            this.$message.success("上传成功");
            this.$api.getDealGatherSettleList(this.settleForm).then(res => {
              console.log(res);
              this.$emit("getDealGatherSettleList", res.data.data);
            });
          });
          this.$refs.upload.clearFiles();
          this.file = "";
          this.stockSiteList = [];
        });
      }
    },
    dgDialogClose() {
      this.$refs.dealgatherRef.resetFields();
      this.$refs.upload.clearFiles();
      this.file = "";
      this.stockSiteList = [];
    },
    // 结算周期对话框展示
    showSettle() {
      this.stockSiteList = [];
      this.settleVisible = true;
    },
    settleDialogClose() {
      this.$refs.settleRef.resetFields();
      this.$refs.upload.clearFiles();
      this.file = "";
      this.stockSiteList = [];
    }
  },
  created() {
    // this.getSelectList();
    // this.getStockSelectList();
    this.getCountryList();
    console.log(this.$refs);
  },
  mounted() {}
};
</script>
<style scoped>
.all-select {
  width: 60%;
  display: flex;
  justify-content: space-between;
  flex-flow: wrap;
}
.select {
  margin: 12px 0 12px 0;
}
.select >>> .el-input {
  width: 10vw;
}
.month {
  flex: 1;
  margin-left: 6px;
}
.select span {
  font-size: 0.8rem;
}
.sort-selection .upload_btn {
  margin-left: 20px;
}
.sort-selection .titleSpan {
  display: inline-block;
  vertical-align: -3px;
}
</style>



