<template>
  <div id="return_list">
    <el-table :data="return_list" border style="width: 96%" stripe>
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="platform" label="渠道"></el-table-column>
      <el-table-column prop="site" label="站点"></el-table-column>
      <el-table-column prop="country" label="国家"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a
            :download="scope.row.file_path"
            :href="
              'https://www.beyoung.group/file_download/?path=' +
                scope.row.file_path.slice(
                  0,
                  scope.row.file_path.lastIndexOf('/')
                ) +
                '/&filename=' +
                scope.row.file_path.slice(
                  scope.row.file_path.lastIndexOf('/') + 1
                )
            "
            >下载</a
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    return_list: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  computed: {
    // 返回列表的长度
    // orderListLength() {
    //   return this.orderList.length;
    // },
    // 判断是交易汇总还是结算周期
    // isType() {
    //   if (this.orderList.length > 0) {
    //     return (
    //       this.orderList[0][7] == "交易报告" ||
    //       this.orderList[0][7] == "汇总报告"
    //     );
    //   }
    // }
  },
  created() {
    console.log(this.return_list);
  }
};
</script>

<style></style>



