<template>
  <div class="order-list">
    <!-- 一行的高度为48 -->
    <el-table
      :data="orderList"
      border
      style="width: 96%"
      :height="orderListLength > 6 ? 336 : null"
      stripe
    >
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="channel" label="渠道"></el-table-column>
      <el-table-column prop="store" label="站点"></el-table-column>
      <el-table-column prop="country" label="国家"></el-table-column>
      <el-table-column prop="date" label="时间"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- <a
            class="download"
            :href="
              'http://www.beyoung.group/order_report_download/?file_path=' + url
            "
            :download="scope.row.file_name"
            >下载</a
          > -->
          <a
            class="download"
            :href="_url3 + 'order_report_download/?file_path=' + url"
            :download="scope.row.file_name"
            >下载</a
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    orderList: {
      type: Array,
      default() {
        return [];
      }
    },
    type: {
      type: String
    },
    selectOpt: {
      type: Object
    }
  },
  components: {},
  data() {
    return {};
  },
  computed: {
    // 返回列表的长度
    orderListLength() {
      return this.orderList.length;
    },
    url() {
      return this.orderList[0].url;
    }
  },
  watch: {},
  methods: {},
  created() {},
  mounted() {}
};
</script>
<style scoped>
.order-list {
  margin-top: 12vh;
}
</style>



