 * @FilePath: \工作项目协同开发\operation_system\src\components\dataBase\settlementchild\OrderListTwo.vue
-->
<template>
  <div class="order-list">
    <!-- 一行的高度为48 -->
    <el-table
      :data="orderList"
      border
      style="width: 96%"
      :height="orderListLength > 6 ? 336 : null"
      stripe
    >
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="1" label="渠道"></el-table-column>
      <el-table-column prop="2" label="站点"></el-table-column>
      <el-table-column prop="3" label="国家"></el-table-column>
      <el-table-column prop="4" label="时间" v-if="isType"></el-table-column>
      <el-table-column
        prop="5"
        label="起始时间"
        v-if="!isType"
      ></el-table-column>
      <el-table-column
        prop="6"
        label="结束时间"
        v-if="!isType"
      ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a
            :download="scope.row[8]"
            :href="
              'https://www.beyoung.group/file_download/?path=' +
                scope.row[8].slice(0, scope.row[8].lastIndexOf('/')) +
                '/&filename=' +
                scope.row[8].slice(scope.row[8].lastIndexOf('/') + 1)
            "
            >下载</a
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    orderList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  components: {},
  data() {
    return {};
  },
  computed: {
    // 返回列表的长度
    orderListLength() {
      return this.orderList.length;
    },
    // 判断是交易汇总还是结算周期
    isType() {
      if (this.orderList.length > 0) {
        return (
          this.orderList[0][7] == "交易报告" ||
          this.orderList[0][7] == "汇总报告"
        );
      }
    }
  },
  watch: {},
  methods: {},
  created() {},
  mounted() {}
};
</script>
<style scoped>
.order-list {
  margin-top: 12vh;
}
</style>



