<template>
  <div class="stock-list">
    <!-- 一行的高度为48 -->
    <el-table :data="stockList" border style="width: 96%">
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column prop="0" label="渠道"></el-table-column>
      <el-table-column prop="1" label="站点"></el-table-column>
      <el-table-column prop="2" label="国家"></el-table-column>
      <el-table-column prop="3" label="时间"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- <el-button type="primary" size="mini" @click="downloadFile(scope.row[4])">下载</el-button> -->
          <a :href="_urlzgl+scope.row[4]">下载</a>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    stockList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  components: {},
  data() {
    return {
      file: ""
    };
  },
  computed: {
    // 返回列表的长度
    stockListLength() {
      return this.stockList.length;
    }
  },
  watch: {},
  methods: {},
  created() {},
  mounted() {}
};
</script>
<style scoped>
.stock-list {
  margin-top: 12vh;
}
</style>


